.contact-page {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 80vh;
  background: linear-gradient(to bottom, #000000,#1e1e1e);
  padding: 50px 20px;
  font-family: 'Trebuchet MS', sans-serif;
  color: white;
}

/* Centered contact card */
.contact-card {
  background: #111;
  border-radius: 25px;
  padding: 40px 30px;
  width: 100%;
  max-width: 500px;
  box-shadow: 0 15px 30px rgba(255, 204, 0, 0.3);
  display: flex;
  flex-direction: column;
  gap: 20px;
}

/* Heading */
.contact-card h2 {
  text-align: center;
  margin-bottom: 20px;
  font-size: 2rem;

  /* Standard fallback color */
  color: #ffcc00;

  /* Gradient background */
  background: linear-gradient(90deg, #ffcc00, #ff4444);

  /* WebKit-only properties */
  -webkit-background-clip: text;
  background-clip: text;
  -webkit-text-fill-color: transparent;
}


/* Input fields */
.contact-card input,
.contact-card textarea {
  background: #222;
  border: none;
  border-radius: 12px;
  padding: 12px 15px;
  font-size: 1rem;
  color: white;
  outline: none;
  resize: none;
  transition: all 0.3s;
}

.contact-card input:focus,
.contact-card textarea:focus {
  box-shadow: 0 0 10px #ffcc00;
  background: #1a1a1a;
}

/* Send button */
.contact-card button {
  background: #ffcc00;
  color: #111;
  font-weight: bold;
  padding: 12px;
  border-radius: 12px;
  border: none;
  cursor: pointer;
  transition: all 0.3s;
}

.contact-card button:hover {
  background: linear-gradient(90deg, #ffcc00, #ff4444);
  transform: scale(1.05);
}
