.modal-overlay {
  position: fixed;
  top: 0; left: 0;
  width: 100%; height: 100%;
  background: rgba(0,0,0,0.7);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
  animation: fadeIn 0.3s ease;
}

@keyframes fadeIn { from {opacity:0;} to {opacity:1;} }

.modal-box {
  background: #1a1a1a;
  color: white;
  padding: 30px;
  border-radius: 15px;
  max-width: 400px;
  width: 90%;
  text-align: center;
  animation: slideIn 0.3s ease;
  position: relative;
}

@keyframes slideIn { from { transform: translateY(-50px); opacity:0; } to { transform: translateY(0); opacity:1; } }

.email-icon {
  width: 60px;
  margin-bottom: 15px;
  animation: bounce 1s infinite;
}

@keyframes bounce { 0%,100% {transform:translateY(0);} 50% {transform:translateY(-10px);} }

.modal-desc {
  color: #ccc;
  margin-bottom: 15px;
  font-size: 0.95rem;
  line-height: 1.4;
}

.modal-input {
  width: 85%;
  padding: 10px;
  margin-bottom: 10px;
  border-radius: 8px;
  border: none;
  outline: none;
}

.modal-note {
  font-size: 0.8rem;
  color: #888;
  margin-bottom: 15px;
  line-height: 1.4;
}

.btn-enroll {
  width: 90%;
  padding: 12px;
  margin-bottom: 20px;
  border: none;
  border-radius: 8px;
  background: linear-gradient(90deg, #ffcc00, #ff4444);
  color: black;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s ease;
}
.btn-enroll:hover {
  transform: scale(1.05);
  color: white;
}

.modal-buttons {
  display: flex;
  justify-content: center;
  gap: 15px;
}

.btn-primary {
  background: #ffcc00;
  color: #111;
  padding: 10px 20px;
  border-radius: 8px;
  font-weight: bold;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-primary:hover {
  background: linear-gradient(to right, #ffcc00, #ff4444);
  color: white;
  transform: scale(1.05);
}

.btn-secondary {
  background: #333;
  color: #fff;
  padding: 10px 20px;
  border-radius: 8px;
  cursor: pointer;
  transition: all 0.3s;
}

.btn-secondary:hover {
  background: #555;
  transform: scale(1.05);
}

.btn-secondary.navigate {
  background: #00ccff;
  color: #111;
}

.btn-secondary.navigate:hover {
  background: linear-gradient(to right, #00ccff, #0077aa);
  color: white;
  transform: scale(1.05);
}
