.certificates-page {
  padding: 50px;
  text-align: center;
  color: white;
  min-height: 80vh;
}

.certificate-grid {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(250px, 1fr));
  gap: 25px;
  margin-top: 30px;
}

.certificate-card {
  background: #1e1e1e;
  padding: 20px;
  border-radius: 12px;
  text-align: center;
  transition: transform 0.3s, box-shadow 0.3s;
}

.certificate-card:hover {
  transform: translateY(-5px);
  box-shadow: 0px 5px 15px rgba(255, 204, 0, 0.5);
}

.certificate-card h3 {
  margin-bottom: 10px;
  font-size: 1.3rem;
}

.certificate-card p {
  font-size: 0.95rem;
  margin-bottom: 15px;
  line-height: 1.4;
}

.certificate-card a {
  display: inline-block;
  padding: 10px 18px;
  background: #ffcc00;
  color: #000;
  font-weight: bold;
  border-radius: 5px;
  text-decoration: none;
  transition: background 0.3s, transform 0.3s;
}

.certificate-card a:hover {
  background: linear-gradient(to right, #ffcc00, red);
  transform: scale(1.05);
}
